import Vue from 'vue'
import moment from 'moment'

Vue.filter('types',function (types) {
  switch (types) {
    case 0:
      return '首页banner';
    case 1:
      return '找职位banner';
    case 2:
      return '找精英banner';
    case 3:
      return '行业大图';
  }
})
Vue.filter('status',function (status) {
    switch(status){
      case 1:
        return '草稿';
      case 2:
        return '上线';
    }
  })
Vue.filter('down',function (down) {
  switch(down){
    case 1:
      return '上线';
    case 2:
      return '下线';
  }
})
Vue.filter('dateformat', function (dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') {
  return moment(dataStr).format(pattern)

})
